.todo-btn {
    width: 5rem;
    display: flex;
    justify-content: space-around;
}

.fa {
    margin-left: -2.5rem;
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    background-color: #fff;
    background-image: none;
    color: #666;
    -webkit-transition: color 0.15s linear;
    transition: color 0.15s linear;
    font-size: 2rem;
}

.fa:hover {
    color: rgb(47, 214, 122);
}

.fa-plus:before {
    content: "\f067";
    font-size: 1.5rem;
}

.showItems {
    margin-top: 3rem;
    text-align: center;
}

.eachItem {
    padding: 1.2rem 1rem;
    border-radius: 0.5rem;
    margin-bottom: 0.7rem;
    margin-left: 1rem;
    min-width: 40rem;
    word-break: break-word;
    background: #3d9845;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.eachItem:nth-child(2) {
    background: rgb(85, 41, 220);
}

.eachItem:hover {
    background: rgb(249, 249, 252);
    color: #3d9845 !important;
}

.showItems .eachItem h3 {
    padding-left: 1rem;
    font-size: 1.6rem;
    color: #fff;
}

.fa-trash-alt,
.fa-edit {
    /* margin-left: -2.5rem; */
    cursor: pointer;
    pointer-events: auto;
    z-index: 10;
    color: rgb(251, 251, 251);
    -webkit-transition: color 0.15s linear;
    transition: color 0.15s linear;
    font-size: 2rem;
}

.addItems .fa-edit {
    color: rgb(47, 214, 122);
}

.fa-edit {
    margin-left: -3.5rem;
}

/*  Please Subscribe to ThapaTechnical Youtube Channel
/* Click Here: https://www.youtube.com/thapatechnical  */

.eachItem:hover .fa-trash-alt {
    color: rgb(214, 47, 61);
}

.eachItem:hover .fa-edit {
    color: rgb(100, 214, 47);
}

.eachItem:hover h3 {
    color: #3d9845;
}

.eachItem:nth-child(2):hover h3 {
    color: rgb(85, 41, 220);
}

.btn {
    letter-spacing: 0.1em;
    cursor: pointer;
    font-size: 14px;
    font-weight: 400;
    line-height: 45px;
    max-width: 160px;
    position: relative;
    text-decoration: none;
    text-transform: uppercase;
    width: 100%;
}

.btn:hover {
    text-decoration: none;
}